#testserver {
  padding: 0px;
  position: fixed;
  z-index: 999;
  background-color: #212121;
  border: 1px solid #d3d3d3;
  text-align: center;
  width: calc(100% - 300px);
  height: 420px;
  color: #fff; }


#testserverheader {
  width: calc(100% - 20px);
  padding: 10px;
  cursor: move;
  z-index: 10;
  background-color: #d3d3d3;
  color: #000;
  -moz-user-select: -moz-none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  position: relative;
  overflow: hidden;

  #testservername {
    margin: 0px;
    position: absolute;
    top: 0px;
    left: 0px;
    overflow: hidden; }

  #testservericons {
    position: absolute;
    right: 0px;
    top: 0px;
    background: rgba(#000,0);
    overflow: hidden;

    i {
      font-size: 18px;
      background: rgba(#000,0);
      cursor: pointer;
      transition: 300ms;

      &:hover {
        background: rgba(#000,0.2); } } } }

#testservercontent {
  width: 100%;
  text-align: left;
  height: calc(100% - 42px);
  overflow: auto; }

#testserverform {
  margin: 0px;
  padding: 0px;
  width: 100%;
  height: 22px;

  input#testserverinput {
    margin: 0px;
    padding: 0px;
    padding-left: 4px;
    border: none;
    width: calc(100% - 65px);
    height: 100%;
    display: inline-block;
    background-color: rgba(#fff,0.2);
    outline: none;
    color: #fff; }

  input#testserversubmit {
    margin: 0px;
    padding: 0px;
    width: 20px;
    height: 100%;
    display: inline-block;
    width: 60px; } }
